.ViewPort {
/*	border: 5px solid red;*/
	margin: 100px auto 0px;
	height: 600px;
	width: 900px;
	overflow: hidden;
	position: relative;
}
.WorldMap {
	background-image: url('../public/assets/001.jpg');
	background-size: contain;
	height: 1240px;
	position: absolute;
	transition: top 100ms, left 100ms;
	width: 1750px;
}
.WorldMap .row {
	display: flex;
	flex-direction: row;
}

.WorldMap .row .cell {
/*	background-color: #bbb;*/
/*	border: 1px solid #ddd;*/
	height: 8px;
	width: 8px;
}

.Player {
	background-image: url('../public/assets/player.jpg');
	height: 28px;
	width: 28px;
	position: absolute;
	transition: top 100ms, left 100ms;
}